.uxora-faq{
  &-item{
    padding-bottom: 24px;
    border-bottom: 1px dashed var(--heading-color);
    margin-bottom: 24px;
    cursor: pointer;
    &.item2{
      padding-bottom: 0;
      border-bottom: none;
      margin-bottom: 0;
      @include max (991px){
        padding-bottom: 24px;
        border-bottom: 1px dashed var(--heading-color);
        margin-bottom: 24px;
      }
    }
    &.item3{
      padding-bottom: 0;
      border-bottom: none;
      margin-bottom: 0;
    }
    &.open {
      .uxora-faq-body{
          display: block;
        }
    }
  }
  &-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 15px;
  }
  &-body{
    display: none;
    p{
      color: var(--light-color5);
    }
  }
  &-wraper{
    display: flex;
    gap: 24px;
    @include max (991px){
      display: block;
    }
  }
  &-data{
    padding-right: 50px;
    flex: 0 0 50%;
    @include max (991px){
      padding-right: 0;
    }
  }
}
.uxora-faq-item.open {
  img.plasicon{
    opacity: 1!important;
  }
  img.mynusicon{
    opacity: 0;
    display: block;
  }
}
.border-b{
  padding-bottom: 0;
  border-bottom: none;
  margin-bottom: 0;
}

img.mynusicon {
  opacity: 1;
  position: relative;
}
img.plasicon {
  opacity: 0;
  position: absolute;
}
.uxora-active-icon{
  flex-shrink: 0;
}



