.uxora-hero{
  &-section{
    padding: 190px 0 106px;
    position: relative;
    @include max (1199px){
      padding: 190px 0 106px;
    }
    @include max (991px){
      padding: 190px 0 66px;
    }
    @include max (767px){
      padding: 130px 0 66px;
    }
  }
  &-content{
    max-width: 1070px;
    p{
      font-size: 21px;
      line-height: 30px;
    }
    @include max (1199px){
      max-width: 800px;
    }
    @include max (991px){
      padding-bottom: 60px;
    }
    @include max (767px){
      padding-bottom: 40px;
    }
  }
  &-btn-wrap{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 24px;
    padding-bottom: 80px;
    @include max (1199px){
      padding-bottom: 60px;
    }
    @include max (767px){
      padding-bottom: 40px;
    }
  }
  &-author-wrap{
    display: flex;
    gap: 15px;
    align-items: center;
    max-width: 360px;
  }
  &-author-thumb{
    flex-shrink: 0;
  }
  &-author-content{
    p{
      font-size: 16px;
      font-weight: 600;
      line-height: 24px;
    }
  }
  
  &-thumb-wrap{
    width: 100%;
    height: 100%;
    border-radius: 17px;
    border: 2px solid var(--heading-color);
    @include max (991px){
      height: auto;
    }
    img{
      width: 100%;
      height: 100%;
      border-radius: 15px;
      object-fit: cover;
    }
  }
  // end one
}
.p-40{
  @include max (991px){
    margin: 24px 0;
  }
}