.uxora-contact{
    &-box{
        max-width: 850px;
        margin: 0 auto;
        h4{
            padding-bottom: 30px;
        }
        p{
            margin: 0;
        }
    }
    &-info-wrap{
        ul{
            display: grid;
            margin-bottom: 24px;
            grid-template-columns: repeat(3, 1fr);
            gap: 24px;
            @include max (1199px){
                grid-template-columns: repeat(2, 1fr);
            }
            @include max (767px){
                grid-template-columns: repeat(1, 1fr);
            }
            li{
                border: 2px solid var(--heading-color);
                padding: 42px 30px;
                border-radius: 20px;
                display: flex;
                align-items: center;
                gap: 24px;
                @include max (991px){
                    padding: 30px 16px;
                    display: block;
                    img{
                        margin-bottom: 10px;
                    }
                }
                @include max (767px){
                    display: flex;
                }
                
                &.wrap2{
                    padding: 26px 30px;
                }
                a{
                    display: flex;
                    align-items: center;
                    gap: 24px;
                    @include max (991px){
                        display: block;
                        img{
                            margin-bottom: 10px;
                        }
                    }
                    @include max (767px){
                        display: flex;
                    }
                }
            }
        }
    }
    &-us-map{
        width: 100%;
        border: 2px solid var(--heading-color);
        border-radius: 10px;
        height: 600px;
        @include max (991px){
            height: 500px;
        }
    }
}
