.uxora-about-us{
    &-thumb{
        img{
            width: 100%;
        }
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
    &-number{
        position: absolute;
        top: 0;
        z-index: 1;
        left: 48%;
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        border-radius: 100px;
        justify-content: center;
        transition: all 0.4s;
        background-color: var(--heading-color);
        h3{
            color: var(--white-color);
        }
        @include max (1399px){
            display: none;
        }
    }
    // add inner page
   
}
.p-15{
    @include max (1199px){
        padding: 15px 0;
    }
    @include max (991px){
        padding: 0;
    }
}

.uxora-about-us{
    &-title {
        font-family: $heading-font;
        font-size: 80px;
        font-weight: 700;
        line-height: 100px;
        letter-spacing: -0.01em;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        @include max (1399px){
            font-size: 65px;
            line-height: 80px;
        }
        @include max (1199px){
            font-size: 48px;
            line-height: 56px;
        }
        @include max (991px){
            font-size: 42px;
            line-height: 48px;
            display: block;
        }
        @include max (767px){
            font-size: 32px;
            line-height: 38px;
        }
        &-thumb{
            position: relative;
            flex-shrink: 0;
            @include max (991px){
                display: none;
            }
            img{
                margin: 0 15px;
            }
            &:hover .uxora-about-us-title-thumb-data {
                opacity: 1;
                visibility: visible;
            }
            &-data{
                background-color: #FCF9F0;
                border-radius: 5px;
                padding: 24px;
                position: absolute;
                width: 438px;
                left: 50%;
                top: 120px;
                z-index: 9;
                opacity: 0;
                text-align: left;
                visibility: hidden;
                transition: all 0.4s;
                transform: translateX(-50%);
                p{
                    color: #000937;
                    font-size:21px;
                    line-height: 30px;
                    margin: 0px 0px 15px 0px;
                }
                h5 {
                    color: #000937;
                    font-weight: 600;
                    font-size: 16px;
                    font-family: "Inter", sans-serif;
                    margin: 0 0 5px 0;
                }
                h6 {
                    color: #999999;
                    font-weight: 600;
                    font-size: 16px;
                    font-family: "Inter", sans-serif;
                    margin: 0;
                }
            }
        }
    }
}

.divided{
    border-bottom: 1px dashed var(--heading-color);
