input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  color:var(--heading-color);
  border-radius: 0;
  background-color: transparent;
  padding: 15px 0 ;
  border: none;
  transition: all 0.4s;
}
textarea {
  height: 145px;
  resize: none;
  &::placeholder{
   color: var(--heading-color)!important;
   }
  
}
.uxora-main{
   &-field{
      input{
         width: 300px;
         padding: 18px 24px;
         border-radius: 10px;
         font-size: 16px;
         line-height: 24px;
         border: 1px solid #00062880;
         border-radius: 3px;
         background-color: var(--white-bg);
         &::placeholder{
            color: #00062880;
         }
         @include max (767px){
            width: 100%;
         }
      }
      &-textarea{
         textarea{
            padding-top: 15px;
            font-size: 16px;
            font-weight: 700;
            line-height: 24px;
            color: var(--heading-color)!important;
            border-bottom: 2px solid var(--heading-color);
         }
      }
      &.field2{
         margin-bottom: 24px;
         p{
            margin-bottom: 10px;
            font-weight: 700;
         }
         input{
            width: 100%;
            border-radius: 3px;
            border: 1px solid transparent;
            background-color: var(--light-color2);
         }
      }
      &.field3{
         margin-bottom: 24px;
         p{
            margin-bottom: 10px;
         }
         input{
            width: 100%;
            border: 1px solid var(--light-color);
         }
      }
   }
   &-field2{
      position: relative;
      input{
         border-radius: 50px;
         padding: 18px 34% 18px 24px;
         background-color: var(--light-color);
         @include max (991px){
            padding: 18px 43% 18px 24px;;
         }
         @include max (767px){
            padding: 18px 30% 18px 24px;;
         }
         @include max (475px){
            padding: 18px 35% 18px 24px;;
         }
         @include max (475px){
            padding: 18px 40% 18px 24px;;
         }
         &::placeholder{
            color: var(--heading-color);
         }
      }
   }
   &-field3{
      font-size: 14px;
      line-height: 20px;
      color: #10141599;
      margin-bottom: 40px;
      &:last-child{
         margin-bottom: 0;
      }
      @include max (767px){
         margin-bottom: 30px;
      }
      p{
         margin: 0;
      }
      input{
         font-size: 16px;
         font-weight: 700;
         line-height: 24px;
         border-bottom: 2px solid var(--heading-color);
         &::placeholder{
            color: var(--heading-color);
         }
      }
   }
}


