.uxora-footer{
  &-menu{
    h3{
      color: var(--light-color);
      margin-bottom: 24px;
    }
    ul{
      li{
        color: var(--light-color);
        margin-bottom: 10px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          position: relative;
          transition: all 0.4s;
          font-size: 21px;
          line-height: 30px;
          color: var(--light-color);
          &::before{
            content: " ";
            left: 0;
            bottom: -3px;
            width: 0;
            height: 1px;
            position: absolute;
            transition: all .4s;
            background: var(--light-color);
          }
          &:hover::before{
            width: 100%;
          }
        }
      }
    }
    &.menu2{
      padding-left: 80px;
      @include max (767px){
        padding-top: 40px;
        padding-left: 0;
      }
    }
    &.menu3{
      padding-left: 110px;
      @include max (1199px){
        padding-left: 0;
      }
      @include max (767px){
        padding-top: 40px;
      }
    }
  }
  &-textarea{
    h3{
      padding-bottom: 20px;
      color: var(--light-color);
    }
    p{
      color: var(--light-color);
    }
    @include max (1399px){
      margin-bottom: 40px;
      max-width: 500px;
    }
   
  }
  &-bottom-wrap{
    margin-top: 80px;
    border-top: 1px dashed #FDF0E733;
    padding: 35px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include max (767px){
      display: block;
      text-align: center;
    }
    @include max (991px){
      margin-top: 80px;
    }
    @include max (767px){
      margin-top: 50px;
    }
  }
  &-bottom-text{
    p{
      color: var(--light-color);
      @include max (767px){
        margin-top: 15px;
        
      }
    }
  }
}
.pl-51{
  padding-left: 50px;
  @include max (1399px){
    padding-left: 0;
  }

}