.uxora-pricing{
    &-wraper{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        @include max (1199px){
            grid-template-columns: repeat(2, 1fr);
        }
        @include max (991px){
            grid-template-columns: repeat(1, 1fr);
        }
    }
    &-wrap{
        padding: 30px;
        border-radius: 0;
        border: 2px solid var(--heading-color);
        @include max (991px){
            border-radius: 20px;
            padding: 30px 20px;
        }
        &:first-child{
            border-radius: 20px 0 0 20px;
            border-right: none;
            @include max (1199px){
                border-radius: 0;
            }
            @include max (991px){
                margin-bottom: 24px;
                border-radius: 20px;
                border-right: 2px solid var(--heading-color);
            }
        }
        &:last-child{
            border-radius: 0 20px 20px 0;
            border-left: none;
             @include max (1199px){
                border-radius: 0;
                margin-top: 24px;
                border-left: 2px solid var(--heading-color);
            }
            @include max (991px){
                border-radius: 20px;
            }
        }
       
    }
    &-header{
        padding-bottom: 24px;
    }
    &-price{
        h2{
            padding-bottom: 15px;
        }
    }
    &-footer{
        p{
            font-weight: 600;
        }
        ul{
            li{
                position: relative;
                padding-bottom: 15px;
                padding-left: 30px;
                &:last-child{
                    padding-bottom: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 0;
                }
            }
        }
    }
}
.mt-15{
    margin-top: 15px;
    @include max (1199px){
        margin-top: 50px;
    }
}