.uxora-video{
    &-thumb{
        position: relative;
        img.thumb{
            border-radius: 20px;
            border: 2px solid var(--heading-color);
        }
        @include max (991px){
            margin-bottom: 40px;
        }
    }
}
  
  
.play-btn {
    position: absolute;
    bottom: -65px;
    left: 80px;
    z-index: 0;
    @include max (991px){
        bottom: auto;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    img{
        @include max (991px){
            width: 80%;
        }
        @include max (991px){
            width: 90px;
        }
    }
}

  
  
  

@keyframes animName {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
  
.waves {
    position: absolute;
    width: 210px;
    height: 210px;
    background: var(--accent-bg2);
    opacity: 0;
    border-radius: 100%;
    left: -40px;
    top: -40px;
    z-index: 0;
    transform: translate(-50px, -50px);
    -webkit-animation: waves 3s ease-in-out infinite;
    animation: waves 3s ease-in-out infinite;
    z-index: -1;
    @include max(991px) {
        width: 160px;
        height: 160px;
        left: -35px;
        top: -35px;
    }

    &.wave-1 {
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
    }
    &.wave-2 {
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
    }
    &.wave-3 {
    -webkit-animation-delay: 2s;
    animation-delay: 2s;
    }
    
}
 
    
    
@keyframes waves {
    0% {
        -webkit-transform: scale(0.2, 0.2);
        transform: scale(0.2, 0.2);
        opacity: 0;
    }

    50% {
        opacity: 0.9;
    }

    100% {
        -webkit-transform: scale(0.9, 0.9);
        transform: scale(0.9, 0.9);
        opacity: 0;
    }
}


  


  