.uxora-t{
    &-icon{
        padding-bottom: 24px;
        img{
            margin: 0 auto;
        }
    }
    &-content{
        padding-bottom: 30px;
        h3{
            color: var(--light-color);
        }
    }
    &-author{
        img{
            margin: 0 auto
        }
        p{
            margin-top: 10px;
            font-size: 16px;
            line-height: 24px;
            color: var(--light-color);
        }
    }
  
}
.uxora-t-slider-init {
    max-width: 1074px;
    text-align: center;
    margin: 0 auto;
    height: 370px;
    @include max (991px){
        height: 450px;
    }
    @include max (767px){
        height: 400px;
    }
    @include max (520px){
        height: 430px;
    }
    @include max (400px){
        height: 460px;
    }
}

.uxora-t-slider-init .uxora-t-slider-next, 
.uxora-t-slider-init .uxora-t-slider-prev {
    bottom: 50%;
    right: -100px;
    width: 60px;
    height: 60px;
    z-index: 1;
    border: 2px solid var(--heading-color);
    display: flex;
    cursor: pointer;
    position: absolute;
    transition: all .4s;
    align-items: center;
    border-radius: 100px;
    background-size: 20px;
    justify-content: center;
    transform: translateY(-50%);
    background-position: center;
    background-repeat: no-repeat;
    background-color: var(--light-color);
    background-image: url(../images/shape/arrow.svg);
    @include max (1399px){
        right: 0;
        bottom: 0;
        top: 20px;
    }
    @include max (1199px){
        top: 85%;
    }
    @include max (767px){
        display: none!important;
    }
}
.uxora-t-slider-init .uxora-t-slider-prev {
    left: -100px;
    bottom: 55%;
    transform: rotate(180deg) !important;
    background-image: url(../images/shape/arrow.svg);
    @include max (1399px){
        left: 0;
        bottom: 0;
        top: -20px;
    }
    @include max (1199px){
        top: 80%;
    }
}

