.uxora-team{
    &-wrap{
        margin-bottom: 24px;
        text-align: center;
        transition: all 0.4s;
        position: relative;
        &:hover{
            .uxora-team{
                &-thumb img{
                    transform: scale(1.1);
                }
            }
            .uxora-social-wrap{
                opacity: 1;
                bottom: 30px;
            }
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        z-index: 0;
        border: 2px solid var(--heading-color);
        margin-bottom: 24px;
        img{
          
            width: 100%;
            height: 100%;
            transition: all 0.4s;
            object-fit: cover;
            border-radius: 10px;
        }
    }
    &-content{
        h3{
            padding-bottom: 12px;
        }
    }
}

.uxora-team-wrap:hover .uxora-team-content a h3::before {
    width: 80%;
}
.uxora-team-content h3::before {
    content: " ";
    left: 20px;
    bottom: 40px;
    width: 0;
    height: 1px;
    position: absolute;
    transition: all .4s;
    background: var(--heading-color);
}