.uxora-iconbox{
  &-wraper{
    display: grid;
    gap: 24px;
    grid-template-columns: repeat(3, 1fr);
    @include max (1199px){
      grid-template-columns: repeat(2, 1fr);
    }
    @include max (991px){
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &-wrap{
    padding: 24px;
    border-radius: 10px;
    transition: all .4s;
    position: relative;
    border: 2px solid var(--heading-color);
    &.wrap2{
      padding: 33px 24px;
    }
  }
  &-icon{
    padding-bottom: 24px;
      
  }
  &-content{
    padding-bottom: 24px;
    h3{
      padding-bottom: 20px;
    }
  }
  &-btn{
    --arrow-hover-move-x: -110%;
    position: relative;
  }
  // end
  &-wrap2{
    padding: 30px;
    display: flex;
    gap: 24px;
    border-radius: 20px;
    margin-bottom: 24px;
    background-color: #FDF0E71A;
  }
  &-number{
    h2{
      color: var(--light-color);
    }
  }
  &-content2{
    h3{
      color: var(--light-color);
      padding-bottom: 20px;
    }
    p{
      color: var(--light-color);
    }
  }
  // end two
  &-wrap3{
    text-align: center;
    margin-bottom: 24px;
    @include max (767px){
      margin-bottom: 24px;
    }
  }
  &-icon3{
    padding-bottom: 24px;
  }
  &-content3{
    h3{
      padding-bottom: 20px;
    }
  }
}
.mt-26{
  margin-top: 26px;
  text-align: center;
}

.uxora-iconbox-btn svg {
  transition: all 0.3s ease-out 0s;
}
.uxora-iconbox-btn:hover svg path:nth-of-type(1), .uxora-iconbox-btn:focus-visible svg path:nth-of-type(1) {
  transform: translateX(calc(-1* var(--arrow-hover-move-x)));
  opacity: 0;
  transition-delay: 0s, 0s;
}
.uxora-iconbox-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.uxora-iconbox-btn svg path {
  transition: transform 0.38s cubic-bezier(0.37, 0.08, 0.02, 0.93), opacity 0.18s ease-out;
}
.uxora-iconbox-btn:hover svg path:nth-of-type(2), .uxora-iconbox-btn:focus-visible svg path:nth-of-type(2) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.uxora-iconbox-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}
.uxora-iconbox-btn svg path:nth-of-type(1) {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 0.15s, 0.15s;
}
.uxora-iconbox-btn svg path:nth-of-type(2) {
  transform: translateX(calc(1* var(--arrow-hover-move-x)));
  opacity: 0.5;
  transition-delay: 0s, 0s;
}


