.uxora-counter{
   &-shape{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-top: -120px;
        justify-content: space-between;
        @include max (1199px){
            margin-top: -300px;
            justify-content: center;
            gap: 50px;
        }
        @include max (991px){
            margin-top: -482px;
        }
        @include max (767px){
            display: none;
        }
    }
}

