.uxora-blog{
    &-wrap{
        border-radius: 20px;
        transition: all 0.4s;
        margin-bottom: 24px;
        &:hover{
            .uxora-blog-thumb img{
                transform: scale(1.1);
            }
            .uxora-blog-content{
                h3{
                    background-size: 100% 1px;
                }
            }
        }
    }
    &-thumb{
        margin-bottom: 24px;
        position: relative;
        overflow: hidden;
        border: 2px solid var(--heading-color);
        border-radius: 20px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
    &-meta{
        text-align: left;
        padding-bottom: 20px;
        @include max (767px){
            padding-bottom: 0;
        }
        ul{
            li{
                display: inline-block;
                margin-right: 24px;
                position: relative;
                &.meta-meta{
                    position: relative;
                    &::before{
                        top: 12px;
                        right: -20px;
                        width: 8px;
                        height: 8px;
                        content: "";
                        position: absolute;
                        border-radius: 100px;
                        background-color: var(--heading-bg);
                    }
                }
                @include max (767px){
                    margin-bottom: 15px;
                }
                &:last-child{
                    margin-right: 0;
                    margin-bottom: 0;
                }
                a{
                    font-size: 16px;
                    line-height: 24px;
                    font-weight: 600;
                    color: var(--heading-color);
                }
            }
        }
    }
    &-content{
        padding-bottom: 20px;
        a{
            color: var(--heading-color);
        }
        h3{
            padding-top: 0;
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0 100%;
            background-repeat: no-repeat;
            background-size: 0 1px;
            transition: background-size .3s ease-in-out;
            display: inline;
        }
        p{
            margin-top: 20px;
            max-width: 600px;
        }
    }
}

blockquote {
    border-radius: 10px;
    padding: 30px;
    text-align: left;
    background-color: var(--heading-bg);
    position: relative;
    max-width: 850px;
    &::before{
        left: 0;
        top: 0;
        width: 8px;
        height: 100%;
        border-radius: 10px 0 0 10px;
        content: "";
        position: absolute;
        background-color: var(--accent-bg2);
    }
    p{
        font-size: 18px;
        line-height: 30px;
        color: var(--light-color);
        font-weight: 600;
    }
    h6{
        font-size: 21px;
        line-height: 30px;
        font-weight: 700;
        color: var(--light-color);
    }
}
